import React from 'react';
import ErrorContainer from './components/ErrorContainer';
import ColumnsContainer from './components/ColumnsContainer';
import VariantContainer from './components/VariantContainer';

import { connect } from 'react-redux';
import getColumns from './state/selectors/getColumns';
import getVariants from './state/selectors/getVariants';

// Use container pattern to decouple conerns from grid
const ColumnListingContainer = connect(getColumns)(ColumnsContainer);
const VariantContainerContainer = connect(getVariants)(VariantContainer);

function App() {
  return ( 
      <ErrorContainer>
        <ColumnListingContainer></ColumnListingContainer>
        <VariantContainerContainer></VariantContainerContainer>
      </ErrorContainer>
  );
}

export default App;
